<template>
  <div class="doughnut-chart-grid">
    <div id="chart-wrapper">
      <canvas id="chart" ref="myChart"></canvas>
    </div>
    <div v-if="chartLabels && myChart" id="doughnut-legend">
      <div
        class="doughnut-legendItem"
        v-for="(label, index) of chartLabels"
        :key="label"
      >
        <div
          :style="
            `backgroundColor: ${myChart.data.datasets[0].backgroundColor[index]}; height: 15px; width: 15px; margin-right: 10px`
          "
        ></div>
        <div class="doughnut-legendItem__name">{{ label }}</div>
        <div class="doughnut-legendItem__spacer"></div>
        <div class="doughnut-legendItem__percentage">
          {{ percentages[index] }}%
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import percentRound from "percent-round";

let ctx;
export default {
  name: "DoughnutChart",
  props: {
    doughnutData: {
      type: Object,
      default: null
    },
    Chart: {
      type: Function
    }
  },
  data() {
    return {
      myChart: null,
      colors: [
        "hsla(207, 46%, 38%, 1)",
        "hsla(184, 54%, 57%, 1)",
        "hsla(205, 28%, 58%, 1)",
        "hsla(143, 40%, 47%, 1)"
      ]
    };
  },
  mounted() {
    let Chart = this.Chart;
    this.$nextTick(() => {
      ctx = this.$refs.myChart.getContext("2d");
      this.myChart = new Chart(ctx, {
        type: "doughnut",
        data: {
          labels: this.chartLabels,
          datasets: [
            {
              // label: "# of Votes",
              data: this.doughnutValues,
              backgroundColor: this.colors,
              borderColor: `rgba(255, 255, 255, 1)`,
              borderWidth: 3
            }
          ]
        },
        options: {
          legend: null,
          cutoutPercentage: 60
        }
      });
    });
  },
  methods: {},
  computed: {
    doughnutValues() {
      return this.doughnutData ? Object.values(this.doughnutData) : null;
    },
    chartLabels() {
      return this.doughnutData ? Object.keys(this.doughnutData) : null;
    },
    percentages() {
      return this.doughnutValues ? percentRound(this.doughnutValues) : null;
    }
  }
};
</script>
