<template>
  <div>
    <doughnut-chart
      v-if="chartType === `doughnut`"
      :doughnutData="doughnutData"
      :Chart="Chart"
    ></doughnut-chart>
    <line-chart
      v-if="chartType === `line`"
      :xAxisLabels="xAxisLabels"
      :xAxisMainLabel="xAxisMainLabel"
      :datasets="datasets"
      :Chart="Chart"
    ></line-chart>
  </div>
</template>

<script>
import Chart from "chart.js";
import DoughnutChart from "./DoughnutChart";
import LineChart from "./LineChart";
// Example of the line chart needed. https://projects.invisionapp.com/share/VCV70G6ZUA2#/screens/398348630
// Example of Doughnut needed. https://invis.io/VCV70G6ZUA2#/397760488_1-5-1_About_Our_Portfolio

/**
The Green:  #4ba86d80 NAV

The Yellow:  #fed10080 PIP ordinary share price

The Light-Blue:  #56c4cc80 MSCI World Total Return (Sterling)

The Dark-Blue:  #20314e80 FTSE All Share Total Return 
 */

// TODO proportional scaling like so: https://css-tricks.com/building-resizeable-components-relative-css-units/
// TODO clarify the .reverse() functions acting on the line dataset.  <-- Reason is that NAV must be first in the Legend, but the chart layers work with last index of datasets drawn on top.
export default {
  data() {
    return {
      Chart
    };
  },
  props: {
    chartType: {
      type: String,
      required: true
    },
    doughnutData: {
      type: Object,
      default: null
    },
    xAxisLabels: {
      type: Array,
      default: null
    },
    datasets: {
      type: Array,
      default: null
    },
    xAxisMainLabel: {
      type: Object,
      default: function() {
        return {
          display: false,
          labelString: "Financial Year",
          fontSize: 16
        };
      }
    }
  },
  mounted() {
    this.integrityCheck(); // Covers basic misuse cases eg. chose doughnut but doesn't have
  },
  methods: {
    integrityCheck() {
      if (this.chartType === "doughnut" && this.datasets !== null) {
        alert(
          "You need a prop called doughnutValues for a doughnut chart, the prop datasets is intended for a line chart with more than one dataset."
        );
      }
      if (this.chartType === "line" && this.doughnutData !== null) {
        alert(
          "You need a prop called datasets for a line chart, the prop doughnutData is intended for a doughnut chart."
        );
      }
      if (this.chartType === "line" && this.xAxisLabels === null) {
        alert(
          "For a Line Chart, you also need to indicate the xAxisLabels for the bottom of the grid"
        );
      }
    }
  },
  components: {
    DoughnutChart,
    LineChart
  }
};
</script>

