<template>
  <div class="line-chart-grid">
    <div id="chart-wrapper">
      <canvas id="chart" ref="myChart"></canvas>
    </div>
    <div v-if="myChart && lineLegendLabels" id="line-legend">
      <div
        class="line-legendItem"
        v-for="(label, index) of lineLegendLabels"
        :key="label"
      >
        <div
          class="line-legend-block"
          :style="
            `backgroundColor: ${
              [...myChart.data.datasets].reverse()[index].backgroundColor
            };`
          "
        ></div>
        <div class="line-legendItem__name">{{ label }}</div>
      </div>
    </div>
  </div>
</template>

<script>
let ctx;
export default {
  name: "LineChart",
  props: {
    xAxisLabels: {
      type: Array,
      default: null
    },
    yAxisStepSize: {
      type: Number,
      default: 1000
    },
    datasets: {
      type: Array,
      default: null
    },
    Chart: {
      type: Function
    },
    xAxisMainLabel: {
      type: Object
    }
  },
  data() {
    return {
      myChart: null,
      colors: [
        "hsla(207, 46%, 38%, 1)",
        "hsla(184, 54%, 57%, 1)",
        "hsla(205, 28%, 58%, 1)",
        "hsla(143, 40%, 47%, 1)"
      ]
    };
  },
  computed: {
    lineLegendLabels() {
      // let reversed = [...this.datasets.map(dataset => dataset.label)].reverse();
      return this.datasets
        ? [...this.datasets.map(dataset => dataset.label)]
        : null;
    }
  },
  mounted() {
    let Chart = this.Chart;
    this.$nextTick(() => {
      ctx = this.$refs.myChart.getContext("2d");
      let defaults = Chart.defaults.global;
      console.log("defaults: ", defaults);
      defaults.elements.line.tension = 0;
      defaults.elements.point.hitRadius = 5;
      this.myChart = new Chart(ctx, {
        type: "line",
        data: {
          labels: this.xAxisLabels,
          datasets: [...this.datasets].reverse()
        },
        options: {
          scales: {
            xAxes: [
              {
                display: true,
                gridLines: {
                  display: false
                },
                scaleLabel: this.xAxisMainLabel
              }
            ],
            yAxes: [
              {
                ticks: {
                  stepSize: this.yAxisStepSize
                }
              }
            ]
          },
          legend: {
            display: false
          }
        }
      });
      console.log("TCL: mounted -> this.myChart.options", this.myChart.options);
    });
  }
};
</script>
